@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Qahiri&display=swap);
*{box-sizing:border-box;margin-right:0;padding:0}body{background-color:#1f3374}.container{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column;color:white;font-family:Helvetica, sans-serif}.container h3{font-family:'Lobster', cursive;font-size:3rem;width:70vw;text-align:center;border-bottom:1px solid gray;margin:0}.board{width:450px;height:450px;background-color:transparent;border:none;display:flex;flex-direction:column}.row{flex:33% 1;display:flex;flex-direction:row;border:none}.square{flex:33% 1;height:100%;cursor:pointer;display:grid;place-items:center;font-size:30px;color:white;font-family:Arial, Helvetica, sans-serif;border:black 1px solid;border-radius:10px}.square:active{background-color:#0e1c49}

